import * as Flickity from 'flickity'
import Vimeo from '@vimeo/player'

import { applyScripts } from '../utils/applyScripts'

if (applyScripts('.home')) {
  const flkty = new Flickity('.js-carousel', {
    prevNextButtons: false,
    pageDots: false
  })

  // const playButton = document.querySelector('.js-play')
  const prevButton = document.querySelector('.js-prev')
  const nextButton = document.querySelector('.js-next')

  prevButton.addEventListener('click', () => {
    flkty.previous()
  })

  nextButton.addEventListener('click', () => {
    flkty.next()
  })

  const slides = document.querySelectorAll('.js-slide')

  slides.forEach(slide => {
    const img = slide.querySelector('.video__img')
    const video = slide.querySelector('.video__video')

    const options = {
      autoplay: true
    }

    const player = new Vimeo(video, options)

    img.addEventListener('click', async () => {
      img.classList.add('is-hidden')
      video.classList.add('is-playing')
      await player.play()
    })
  })
}

